import React, {useState} from "react"
import {
    Modal,
    Button,
    List,
    Input,
    Switch
} from 'antd'
import styled from '@emotion/styled'

import Url from './../../../url/url.js'
import {swallErr, swallGood} from './../../../alert/alert.js'

const ModalRoleCreation = ({dataRole, setDataRole, setSkeletonShow}) => {

    const axios = require('axios'); // AJAX

    const [isModalVisible, setIsModalVisible] = useState(false);
    const [ValueName, setValueName] = useState('')

    let actionPermissionsForm = {},
        dataPermissionsForm = {
            forPupil: {
                phone: undefined,
                parentPhone: undefined,
                parentNSM: undefined,
                balance: undefined,
                discord: undefined,
                paymentHistory: undefined,
                notes: undefined,
                localSchedule: undefined,
                name: 1,
                surname: 1,
                midname: 1
            },
            forGroup: {
                groupsHistory: undefined,
                groups: undefined,
                tutors: undefined,
                GROUP_NAME: undefined,
                LEVEL: undefined,
                TUTOR: undefined,
                PUPILS: undefined,
                PLACES: undefined,
                GLOBAL_SCHEDULE: undefined
            }
        },
        nameRole = ValueName

    const showModal = () => {
        setIsModalVisible(true);
        setValueName('')
    };

    const handleOk = () => {

        if (Object.keys({...actionPermissionsForm}).length && nameRole) {

            setSkeletonShow(true)

            axios({
                method: 'post',
                url: `${Url}/AdminPanel/Roles`,
                headers: {
                    'Content-Type': 'application/json;charset=utf-8'
                },
                data: {
                    name: nameRole,
                    actionPermissions: Object.values(actionPermissionsForm),
                    dataPermissions: {...dataPermissionsForm}
                }
            })
            .then(res => {
                let {data} = res
                let box = [...dataRole, data]

                swallGood('–ì–æ—Ç–æ–≤–æ', `–†–æ–ª—å "${data.name}" —Å–æ–∑–¥–∞–Ω–∞`)
                setDataRole(box)
                setSkeletonShow(false)
            })
            .catch(error => {
                if (error.response) {
                    setSkeletonShow(false)
                }
            })

            setIsModalVisible(false)
        } else {
            swallErr('–û–π...', '–ü—Ä–∏–¥—É–º–∞–π—Ç–µ –Ω–∞–∑–≤–∞–Ω–∏–µ –∏ –≤—ã–±–∏—Ä–∏—Ç–µ —Ö–æ—Ç—è–±—ã –æ–¥–Ω–æ –¥–µ–π—Å—Ç–≤–∏–µ')
            setValueName(nameRole)
        }
    };

    const handleCancel = () => {
        setIsModalVisible(false);
    }

    let actionPermissions = [
        ['–°–æ–∑–¥–∞—Ç—å –≥—Ä—É–ø–ø—É', 0],
        ['–ò–∑–º–µ–Ω–∏—Ç—å –≥—Ä—É–ø–ø—É', 1],
        ['–£–¥–∞–ª–∏—Ç—å –≥—Ä—É–ø–ø—É', 2],
        ['–ü–æ–ª—É—á–∏—Ç—å —Å–ø–∏—Å–æ–∫ –≥—Ä—É–ø–ø', 3],
        ['–ü—Ä–æ—Å–º–æ—Ç—Ä–µ—Ç—å —Å—Ç—Ä–∞–Ω–∏—Ü—É –≥—Ä—É–ø–ø—ã', 4],
        ['–£—Å—Ç–∞–Ω–æ–≤–∏—Ç—å —Ä–∞—Å–ø–∏—Å–∞–Ω–∏–µ –≥—Ä—É–ø–ø—ã', 5],
        ['–û–±–Ω–æ–≤–∏—Ç—å —Ä–∞—Å–ø–∏—Å–∞–Ω–∏–µ —É—á–µ–Ω–∏–∫–æ–≤', 6],
        ['–î–æ–±–∞–≤–∏—Ç—å —É—á–µ–Ω–∏–∫–æ–≤ –≤ –≥—Ä—É–ø–ø—É', 7],
        ['–£–¥–∞–ª–∏—Ç—å —É—á–µ–Ω–∏–∫–∞ –∏–∑ –≥—Ä—É–ø–ø—ã', 8],
        ['–î–æ–±–∞–≤–∏—Ç—å —É—á–∏—Ç–µ–ª—è', 9],
        ['–°–æ–∑–¥–∞—Ç—å —É—á–µ–Ω–∏–∫–∞', 10],
        ['–ò–∑–º–µ–Ω–∏—Ç—å —É—á–µ–Ω–∏–∫–∞', 11],
        ['–£–¥–∞–ª–∏—Ç—å —É—á–µ–Ω–∏–∫–∞', 12],
        ['–ü–æ–ª—É—á–∏—Ç—å —Å–ø–∏—Å–æ–∫ —É—á–µ–Ω–∏–∫–æ–≤', 13],
        ['–ó–∞–π—Ç–∏ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—É —É—á–µ–Ω–∏–∫–∞', 14],
        ['–ò–º–ø–æ—Ä—Ç–∏—Ä–æ–≤–∞—Ç—å —Ñ–∞–π–ª', 15],
        ['–°–æ–∑–¥–∞—Ç—å –ø–ª–∞—Ç–µ–∂', 16],
        ['–°–æ–∑–¥–∞—Ç—å –∑–∞–º–µ—Ç–∫—É', 17],
        ['–£–¥–∞–ª–∏—Ç—å –∑–∞–º–µ—Ç–∫—É', 18],
        ['–ò—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å –ø–æ–∏—Å–∫', 19]
    ].map(actionData => (
        <List.Item key={actionData[1]}>
            {actionData[0]}
            <Switch
                onChange={checked => {
                    if (checked) {
                        actionPermissionsForm[actionData[0]] = actionData[1]
                    } else {
                        delete actionPermissionsForm[actionData[0]]
                    }
                }}
                style={{position: "absolute", right: "15px"}}/>
        </List.Item>
    ))

    let dataPermissionsPupils = [
        ['–¢–µ–ª–µ—Ñ–æ–Ω', 'phone'],
        ['–†–æ–¥–∏—Ç–µ–ª—å—Å–∫–∏–π —Ç–µ–ª–µ—Ñ–æ–Ω', 'parentPhone'],
        ['–†–æ–¥–∏—Ç–µ–ª—å—Å–∫–∏–µ –∏–Ω–∏—Ü–∏–∞–ª—ã', 'parentNSM'],
        ['–ë–∞–ª–∞–Ω—Å', 'balance'],
        ['Discord', 'discord'],
        ['–ò—Å—Ç–æ—Ä–∏—é –ø–ª–∞—Ç–µ–∂–µ–π', 'paymentHistory'],
        ['–ó–∞–º–µ—Ç–∫–∏', 'notes'],
        ['–†–∞—Å–ø–∏—Å–∞–Ω–∏–µ —É—á–µ–Ω–∏–∫–∞', 'localSchedule'],

    ].map(SeeData => (
        <List.Item key={SeeData[1]}>
            {SeeData[0]}
            <Switch
                onChange={checked => {
                    if (checked) {
                        dataPermissionsForm.forPupil[SeeData[1]] = 1
                    } else {
                        dataPermissionsForm.forPupil[SeeData[1]] = undefined
                    }
                }}
                style={{position: "absolute", right: "15px"}}/>
        </List.Item>
    ))

    let dataPermissionsGroups = [
        ['–ò—Å—Ç–æ—Ä–∏—é –≥—Ä—É–ø–ø', 'groupsHistory'],
        ['–°–ø–∏—Å–æ–∫ –≥—Ä—É–ø–ø', 'groups'],
        ['–£—á–∏—Ç–µ–ª–µ–π', 'tutors'],
        ['–ò–º—è –≥—Ä—É–ø–ø—ã', 'GROUP_NAME'],
        ['–£—Ä–æ–≤–µ–Ω—å –≥—Ä—É–ø–ø—ã', 'LEVEL'],
        ['–£—á–∏—Ç–µ–ª–µ–π –≥—Ä—É–ø–ø—ã', 'TUTOR'],
        ['–£—á–µ–Ω–∏–∫–æ–≤ –≥—Ä—É–ø–ø—ã', 'PUPILS'],
        ['–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –º–µ—Å—Ç –≤ –≥—Ä—É–ø–ø–µ', 'PLACES'],
        ['–†–∞—Å–ø–∏—Å–∞–Ω–∏–µ –≥—Ä—É–ø–ø—ã', 'GLOBAL_SCHEDULE']
    ].map(SeeData => (
        <List.Item key={SeeData[1]}>
            {SeeData[0]}
            <Switch
                onChange={checked => {
                    if (checked) {
                        dataPermissionsForm.forGroup[SeeData[1]] = 1
                    } else {
                        dataPermissionsForm.forGroup[SeeData[1]] = undefined
                    }
                }}
                style={{position: "absolute", right: "15px"}}/>
        </List.Item>
    ))

    const CreationRole = styled.div({
        display: 'inline',
        '& > button': {
            marginLeft: '10px',
            fontSize: '13px',
            height: '30px'
        }
    })

    return (
        <CreationRole>
            <Button type="primary" onClick={showModal}>
                –°–æ–∑–¥–∞—Ç—å —Ä–æ–ª—å
            </Button>
            <Modal
                width={1000}
                title="–°–æ–∑–¥–∞–Ω–∏–µ —Ä–æ–ª–∏"
                visible={isModalVisible}
                onOk={handleOk}
                onCancel={handleCancel}
            >
                <List

                    size="small"
                    bordered={false}
                >
                    <List.Item>
                        –ù–∞–∑–≤–∞–Ω–∏–µ —Ä–æ–ª–∏: <Input onChange={(e) => {
                        nameRole = e.target.value
                    }} defaultValue={ValueName}/>
                    </List.Item>
                    <div style={{display: 'flex'}}>
                        <div className="boxLeft col-6" style={{position: 'relative'}}>
                            <List.Item style={{fontSize: "16px", fontWeight: "500"}}>
                                –†–∞–∑—Ä–µ—à—ë–Ω–Ω—ã–µ –¥–µ–π—Å—Ç–≤–∏—è:
                            </List.Item>
                            {actionPermissions}
                        </div>
                        <div className="boxRights col-6">
                            <List.Item style={{fontSize: "16px", fontWeight: "500"}}>
                                –ú–æ–∂–Ω–æ –≤–∏–¥–µ—Ç—å:
                            </List.Item>
                            {dataPermissionsPupils}
                            {dataPermissionsGroups}
                        </div>
                    </div>
                </List>
            </Modal>
        </CreationRole>
    )
}

export default ModalRoleCreation